html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "Verdana";
  font-style: normal;
  font-weight: 400;
  src: local("Verdana"), local("Verdana"), url("../fonts/Verdana.woff2") format("woff2"), url("../fonts/Verdana.woff") format("woff");
}

@font-face {
  font-family: "Verdana";
  font-style: italic;
  font-weight: 400;
  src: local("Verdana-Italic"), local("Verdana-Italic"), url("../fonts/Verdana-Italic.woff2") format("woff2"), url("../fonts/Verdana-Italic.woff") format("woff");
}

@font-face {
  font-family: "Verdana";
  font-style: normal;
  font-weight: 700;
  src: local("Verdana-Bold"), local("Verdana-Bold"), url("../fonts/Verdana-Bold.woff2") format("woff2"), url("../fonts/Verdana-Bold.woff") format("woff");
}

@font-face {
  font-family: "Verdana";
  font-style: italic;
  font-weight: 700;
  src: local("Verdana-BoldItalic"), local("Verdana-BoldItalic"), url("../fonts/Verdana-BoldItalic.woff2") format("woff2"), url("../fonts/Verdana-BoldItalic.woff") format("woff");
}

.icon-basket {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

.icon-user {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #f8f8fb;
  line-height: 1.90;
  letter-spacing: 0.063em;
  font-family: 'Verdana', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-weight: 400;
  font-size: 15px;
  color: #383838;
  min-width: 320px;
  margin: 0;
  padding: 0;
  border: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: currentColor;
}

a:hover {
  text-decoration: none;
}

.inner {
  max-width: 1420px;
  margin: 0 auto;
  padding: 0 30px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.btn {
  display: inline-block;
  border: none;
  color: #fff;
  text-decoration: none;
  background-color: #ff833e;
  padding: 1.33em 1.8em;
  font-size: 15px;
  font-family: 'Verdana', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-weight: 400;
  letter-spacing: 0.066em;
  border-radius: 10px;
  text-align: center;
  vertical-align: top;
  position: relative;
  outline: none;
  transition: background-color 1s, box-shadow .3s;
  line-height: 1.4;
  cursor: pointer;
  box-shadow: 0 12px 20px -6px #ff833e;
}

.no-touch .btn:hover {
  background-color: #ff7325;
  box-shadow: none;
}

.btn_bold {
  text-transform: uppercase;
  font-weight: 700;
}

.btn_md {
  min-width: 280px;
}

.course {
  padding: 1.7em 2em;
  background-color: #fff;
  box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
  min-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 317px;
  width: 100%;
  border-radius: 10px;
  transition: background-color .4s, color .4s, box-shadow .4s;
}

.no-touch .course:hover {
  background-color: #ff833e;
  color: #fff;
  box-shadow: 0 25px 42px -14px #ff833e;
}

.no-touch .course:hover .course__aside {
  color: #fff;
}

.no-touch .course:hover .course__price:before {
  background-color: #fff;
}

.no-touch .course:hover .course__title.title {
  color: #fff;
}

.course__aside {
  color: #9d9d9d;
  transition: color .4s;
}

.course__bottom {
  display: flex;
  padding-top: 1em;
  font-size: 1.2em;
  font-weight: 700;
  letter-spacing: normal;
}

.course__price {
  margin-left: 30px;
  position: relative;
}

.course__price:before {
  content: '';
  position: absolute;
  height: 7px;
  width: 7px;
  border-radius: 50%;
  background-color: #000;
  left: -18.5px;
  top: 50%;
  margin-top: -3.5px;
  transition: background-color .4s;
}

.course__title {
  margin-top: .4em;
  transition: color .4s;
}

.event {
  max-width: 433px;
  width: 100%;
  color: #000;
}

.event__img img {
  max-width: 100%;
  display: block;
}

.event__date {
  font-size: 16px;
  line-height: 1.75;
  padding-top: 1.25em;
  letter-spacing: normal;
}

.event__date span {
  text-transform: uppercase;
  color: #ff833e;
  font-weight: 700;
}

.event__title {
  max-width: 300px;
  margin-top: 0.25em;
}

.instructor {
  background-color: #fff;
  max-width: 433px;
  width: 100%;
  box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  overflow: hidden;
}

.instructor__top {
  background-color: #0053a5;
  padding-bottom: 34%;
  position: relative;
  overflow: hidden;
}

.instructor__top:before {
  content: '';
  position: absolute;
  top: 1.33em;
  left: 2em;
  right: 2em;
  bottom: -1em;
  border: 1px solid #fff;
  border-radius: 10px;
}

.instructor__bottom {
  text-align: center;
  padding: 0 2em 2.66em;
}

.instructor__bottom p {
  text-align: left;
  line-height: 1.9;
}

.instructor__img {
  height: 120px;
  width: 120px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  margin: -5.7em auto 0;
  overflow: hidden;
  z-index: 1;
  position: relative;
}

.instructor__title {
  margin-top: 0.5em;
}

.instructor__aside {
  letter-spacing: 0.063em;
  color: #9d9d9d;
  margin-bottom: 1em;
}

.testimonial {
  background-color: #fff;
  padding: 2em 2em 2.66em;
  max-width: 433px;
  width: 100%;
  box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

.testimonial__top {
  display: flex;
  align-items: center;
}

.testimonial__img {
  flex-shrink: 0;
  height: 90px;
  width: 90px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  overflow: hidden;
  margin-right: 1.33em;
}

.testimonial__aside {
  color: #9d9d9d;
  letter-spacing: 0.033em;
}

.testimonial__bottom {
  position: relative;
  margin-top: 1.6em;
}

.testimonial__bottom:before {
  content: '';
  position: absolute;
  width: 54px;
  height: 38px;
  background-image: url(../img/quotes.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  top: 9px;
  left: 0;
}

.testimonial__bottom p {
  z-index: 1;
  position: relative;
}

.testimonial__bottom p:first-child {
  padding: 0 2em;
}

.feedback {
  padding: 2em;
  background-color: #0053a5;
}

.feedback__inner {
  border: 2px solid #fff;
  padding: 2.36em 4em;
}

.feedback__title {
  text-align: center;
  margin-bottom: 1.28em;
}

.feedback__title.title {
  color: #fff;
}

.feedback__check {
  text-align: center;
  margin-top: 2.66em;
  color: #fff;
  letter-spacing: normal;
}

.form {
  display: flex;
  justify-content: center;
  align-items: center;
}

.form__btn {
  padding-left: 10px;
}

.form__btn .btn {
  box-shadow: none;
}

.form-group {
  max-width: 300px;
  width: 100%;
  padding: 0 10px;
}

.form-group input {
  color: #9d9d9d;
  font-family: 'Verdana', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.066em;
  padding: 1.33em;
  background-color: #fff;
  border-radius: 10px;
  width: 100%;
}

.form-group input:focus::placeholder {
  font-size: 0;
}

.form-group input::placeholder {
  color: #9d9d9d;
  font-family: 'Verdana', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.066em;
}

.checkbox {
  position: relative;
  cursor: pointer;
}

.checkbox__icon {
  position: absolute;
  top: -5px;
  left: -39px;
  width: 25px;
  height: 25px;
  border-radius: 4px;
  border: 1px solid #fff;
}

.checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.checkbox input:checked ~ .checkbox__icon {
  background-image: url(../img/check.svg);
  background-size: 70%;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.seminar {
  max-width: 433px;
  width: 100%;
  color: #000;
}

.seminar__top {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  border-radius: 10px;
  height: 0;
  padding-bottom: 57.75%;
  box-shadow: 0 20px 42px -24px rgba(0, 0, 0, 0.3);
  position: relative;
}

.seminar__btn {
  position: absolute;
  left: 0;
  bottom: 0;
}

.seminar__list {
  box-shadow: 0 20px 42px -18px rgba(0, 0, 0, 0.24);
  background-color: #fff;
  border-radius: 10px;
  margin-top: 20px;
}

.seminar__item {
  padding: 1.8em 2em;
  font-weight: 700;
  letter-spacing: 0.033em;
  line-height: 1.66;
}

.seminar__item:not(:last-child) {
  border-bottom: 1px solid #bdbdbf;
}

.map {
  width: 100%;
  height: 533px;
}

.contacts {
  color: #000;
  max-width: 665px;
  width: 100%;
  position: relative;
}

.contacts__info {
  position: absolute;
  bottom: -127px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  background-color: #fff;
  max-width: 350px;
  width: 100%;
  padding: 2em;
  border-radius: 10px;
  box-shadow: 0 20px 56px -33px rgba(0, 0, 0, 0.2);
}

.contacts__aside {
  margin-top: 0.8em;
  margin-bottom: 0.1em;
  color: #9d9d9d;
}

.contacts a {
  border-bottom: 1px solid transparent;
  transition: border-color .3s;
}

.no-touch .contacts a:hover {
  border-color: #000;
}

.contacts address {
  font-style: normal;
  line-height: 1.73;
}

.header {
  background-color: #212121;
  box-shadow: 0 8px 65px -45px rgba(0, 0, 0, 0.5);
}

.header__absolute {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  box-shadow: none;
  color: #fff;
}

.header__top {
  padding: 1.6em 0;
  text-transform: uppercase;
  opacity: .5;
  font-size: 10px;
  letter-spacing: 0.025em;
  display: flex;
  justify-content: center;
}

.header__top a {
  border-bottom: 1px solid transparent;
  transition: border-color .3s;
}

.no-touch .header__top a:hover {
  border-color: currentColor;
}

.header__absolute .header__top {
  opacity: 1;
}

.header__contacts {
  margin-left: 26px;
  display: flex;
}

.header__phone {
  position: relative;
}

.header__phone:not(:last-child) {
  padding-right: 45px;
}

.header__phone:after {
  content: '';
  position: absolute;
  height: 5px;
  width: 5px;
  border-radius: 50%;
  background-color: currentColor;
  top: 50%;
  margin-top: -2.5px;
  right: 20px;
}

.header__main {
  background-color: #fff;
  padding: 1.5em 0;
}

.header__main .inner {
  display: flex;
  align-items: center;
}

.header__absolute .header__main {
  background-color: transparent;
}

.header__logo {
  margin-right: 1.5em;
}

.header__group {
  display: flex;
  align-items: center;
  margin-left: auto;
}

.header__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  opacity: 0.5;
  transition: opacity .3s;
  border: 2px solid currentColor;
  width: 2.5em;
  height: 2.5em;
  border-radius: 10px;
  margin-right: 20px;
}

.no-touch .header__icon:hover {
  opacity: 1;
}

.header__absolute .header__icon {
  opacity: 1;
}

.header__btn .btn {
  letter-spacing: 0.062em;
  padding: 1em 1.8em;
}

.logo a {
  display: block;
}

.logo img {
  max-width: 80px;
  width: 100%;
  display: block;
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__item:not(:first-child) {
  margin-left: 2.7em;
}

.nav__link {
  opacity: 0.3;
  position: relative;
  padding: 2px 0;
  transition: opacity .3s;
}

.nav__link:before {
  content: '';
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: currentColor;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform .5s;
}

.no-touch .nav__link:hover {
  opacity: 1;
}

.no-touch .nav__link:hover:before {
  transform: scaleX(1);
  transform-origin: left;
}

.header__absolute .nav__link {
  opacity: 1;
}

.title {
  font-family: 'Verdana', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-size: 44px;
  font-weight: 700;
  line-height: 1.23;
  color: #000;
  letter-spacing: normal;
}

.title_lg {
  font-size: 30px;
  line-height: 1.48;
}

.title_md {
  font-size: 21px;
  line-height: 1.48;
}
