.btn {
	display: inline-block;
	border: none;
	color: $white;
	text-decoration: none;
	background-color: $orange;
	padding: 1.33em 1.8em;
	font-size: 15px;
	@include verdana;
	font-weight: 400;
	letter-spacing: 0.066em;
	border-radius: 10px;
	text-align: center;
	vertical-align: top;
	position: relative;
	outline: none;
	transition: background-color 1s, box-shadow .3s;
	line-height: 1.4;
	cursor: pointer;
	box-shadow: 0 12px 20px -6px $orange;
	@include hover {
		background-color: darken($orange, 5%);
		box-shadow: none; }
	&_bold {
		text-transform: uppercase;
		font-weight: 700; }
	&_md {
		min-width: 280px; } }
