.header {
	background-color: #212121; // 								REMOVE
	box-shadow: 0 8px 65px -45px rgba(0, 0, 0, 0.5);
	&__absolute {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		box-shadow: none;
		color: $white; }
	&__top {
		padding: 1.6em 0;
		text-transform: uppercase;
		opacity: .5;
		font-size: 10px;
		letter-spacing: 0.025em;
		display: flex;
		justify-content: center;
		a {
			border-bottom: 1px solid transparent;
			transition: border-color .3s;
			@include hover {
				border-color: currentColor; } }
		.header__absolute & {
			opacity: 1; } }
	&__contacts {
		margin-left: 26px;
		display: flex; }
	&__phone {
		position: relative;
		&:not(:last-child) {
			padding-right: 45px; }
		&:after {
			content: '';
			position: absolute;
			@include circle(5);
			background-color: currentColor;
			top: 50%;
			margin-top: -2.5px;
			right: 20px; } }
	&__main {
		background-color: $white;
		padding: 1.5em 0;
		.inner {
			display: flex;
			align-items: center; }
		.header__absolute & {
			background-color: transparent; } }
	&__logo {
		margin-right: 1.5em; }
	&__nav {}
	&__group {
		display: flex;
		align-items: center;
		margin-left: auto; }
	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 20px;
		opacity: 0.5;
		transition: opacity .3s;
		border: 2px solid currentColor;
		// padding: 0.75em
		width: 2.5em;
		height: 2.5em;
		border-radius: 10px;
		margin-right: 20px;
		@include hover {
			opacity: 1; }
		.header__absolute & {
			opacity: 1; } }
	&__btn {
		.btn {
			letter-spacing: 0.062em;
			padding: 1em 1.8em; } } }

.logo {
	a {
		display: block; }
	img {
		max-width: 80px;
		width: 100%;
		display: block; } }

.nav {
	&__list {
		display: flex;
		align-items: center; }
	&__item {
		&:not(:first-child) {
			margin-left: 2.7em; } }
	&__link {
		opacity: 0.3;
		position: relative;
		padding: 2px 0;
		transition: opacity .3s;
		&:before {
			content: '';
			position: absolute;
			top: 100%;
			left: 0;
			width: 100%;
			height: 2px;
			background-color: currentColor;
			transform: scaleX(0);
			transform-origin: right;
			transition: transform .5s; }
		@include hover {
			opacity: 1;
			&:before {
				transform: scaleX(1);
				transform-origin: left; } }
		.header__absolute & {
			opacity: 1; } } }
